rt 'utility';\n@import 'split_pane';\n\n#inquirer-app {\n\n  position: fixed;\n  top: 0px;\n  right: 0px;\n  bottom: 0px;\n  left: 0px;\n\n  font-family: $root-font;\n\n  #inquirer-header {\n    position: absolute;\n    top: 0px;\n    right: 0px;\n    left: 0px;\n\n    background-color: #005172;\n    box-shadow: 0 1px 2px 0 grey;\n\n    padding: 0px 10px;\n\n    color: white;\n\n    height: $header-height;\n    line-height: $header-height;\n\n    vertical-align: middle;\n\n    z-index: 1001;\n  }\n\n  #inquirer-body {\n    position: absolute;\n    top: $header-height;\n    right: 0px;\n    bottom: 0px;\n    left: 0px;\n\n    #inquirer-loader {\n      width: 100%;\n      padding-top: 50px;\n      text-align: center;\n\n      > div {\n        display: inline-block;\n      }\n    }\n\n    #inquirer-sidebar {\n      position: absolute;\n      top: 0px;\n      bottom: 0px;\n      left: 0px;\n\n      //border-right: 1px solid grey;\n      box-shadow: 1px 0 2px 0 grey;\n\n      //overflow-x: scroll;\n      overflow: auto;\n      \n      width: $sidebar-width;\n\n      @import 'sidebar';\n\n      z-index: 1000;\n      //background-color: lightsteelblue;\n    }\n\n    #inquirer-content {\n      position: absolute;\n      top: 0px;\n      right: 0px;\n      bottom: 0px;\n      left: $sidebar-width;\n\n      @import 'content';\n\n    }\n  }\n\n  //Styles for types\n  .NON_NULL {\n    &::after {\n      content: \"!\";\n      color: black;\n    }\n  }\n\n  .LIST {\n    &::before {\n      content: \"[\";\n      color: black;\n    }\n    &::after {\n      content: \"]\";\n      color: black;\n    }\n  }\n\n}",".sidebar-section {\n\n  .sidebar-section-title {\n    font-size: 16px;\n    font-weight: bold;\n    padding: 5px 10px;\n\n    border: 1px solid gainsboro;\n    border-left: none;\n    border-right: none;\n  }\n\n  .sidebar-item {\n    padding: 10px 10px;\n    display: block;\n    text-decoration: none;\n\n    font-size: 12px;\n    font-weight: bold;\n    font-family: $codeFontFamily;\n    color: black;\n\n    .sidebar-item-name {\n      color: $queryNameColor;\n    }\n\n    .sidebar-item-type {\n      margin: 5px 0;\n      padding-left: 10px;\n      color: $typeNameColor;\n    }\n\n    .sidebar-item-description {\n      font-size: 10px;\n      font-weight: normal;\n    }\n\n    &:not(:last-of-type) {\n      border-bottom: 1px solid gainsboro;\n    }\n\n    &.active {\n      background-color: gainsboro;\n    }\n  }\n}","$maxWidth: 990px;\n$toolbarHeight: 60px;\n\n\n@mixin operationResult() {\n  display: block;\n  float: left;\n  //width: 50%;\n  height: 100%;\n\n  //overflow: scroll;\n\n  > div {\n    white-space: pre;\n    display: block;\n    height: 100%;\n  }\n}\n\n#editor-container {\n  position: absolute;\n  width: 100%;\n  height: 100%;\n\n  padding-top: $toolbarHeight;\n\n  box-sizing: border-box;\n\n  .editor-toolbar {\n    position: absolute;\n    background-color: whitesmoke;\n    border-bottom: 1px solid grey;\n\n    box-shadow: 0px 1px 2px 0px grey;\n\n    z-index: 999;\n    \n    position: absolute;\n    top: 0px;\n    left: 0px;\n    right: 0px;\n\n    padding: 0px 20px;\n\n    font-family: $codeFontFamily;\n\n    height: $toolbarHeight;\n    line-height: $toolbarHeight;\n\n    * {\n      vertical-align: middle;\n    }\n\n    .action-toolbar {\n\n      height: 100%;\n      line-height: $toolbarHeight;\n\n      display: flex;\n      justify-content: space-between;\n      align-items: center;\n    \n      .action-header {\n        font-size: 18px;\n\n        display: inline-block;\n        line-height: 1em;\n\n        div {\n          display: inline-block;\n          &.action-name {\n            color: $queryNameColor\n          }\n          &.action-type {\n            color: $typeNameColor;\n            margin-left: 10px;\n          }\n        }\n        \n        p {\n          font-size: 12px;\n          margin: 0px;\n        }\n      }\n\n      .editor-controls {\n        text-align: right;\n        display: inline-block;\n        \n        line-height: 1em;\n\n        .editor-control-picker {\n          display: inline-block;\n          margin-right: 30px;\n        }\n\n        button {\n          padding: 8px 16px;\n          border: 1px solid grey;\n          background-color: whitesmoke;\n          border-radius: 4px;\n\n          &:disabled {\n            background-color: whitesmoke;\n            border-color: gainsboro;\n            color: gainsboro;\n          }\n        }\n      }\n    }\n\n  } // api-main-toolbar\n\n  #editor-panes {\n    position: relative;\n    width: 100%;\n    height: 100%;\n\n    @import 'creator_main';\n\n    #operation-generated {\n\n      position: absolute;\n      top: 0;\n      right: 0;\n      bottom: 0;\n      left: 0;\n\n      float: left;\n      padding: 0px;\n      margin: 0px;\n      overflow: hidden;\n\n      font-family: $codeFontFamily;\n\n      .generated-content {\n        position: relative;\n        height: 100%;\n        padding: 0px;\n        margin: 0px;\n        overflow: hidden;\n\n        .operation-result-gql {\n          width: 100%;\n          height: 100%;\n\n          padding: 0px;\n          margin: 0px;\n          overflow: hidden;\n\n          @include operationResult();  \n          padding-right: 2px;\n\n          .gql-panel {\n          \n            padding: 10px;\n            overflow: auto;\n\n            &.invalid {\n              color: lightgrey;\n              //text-decoration-style: 1px dashed red;\n            }\n          }\n        }\n      }\n    }\n        .copy-button {\n          position: absolute;\n          top: 10px;\n          right: 25px;\n\n          button {\n            padding: 8px 16px;\n            border: 1px solid grey;\n            background-color: whitesmoke;\n            border-radius: 4px;\n            line-height: 1em;\n          }\n        }\n\n    #editor-result {\n      \n      position: absolute;\n      top: 0;\n      right: 0;\n      bottom: 0;\n      left: 0;\n\n//      height: 100%;\n      float: left;\n      padding: 0px;\n      margin: 0px;\n      overflow: hidden;\n\n      font-family: $codeFontFamily;\n\n      .result-data {\n        width: 100%;\n        height: 100%;\n\n        padding: 10px;\n        overflow: auto;\n\n        white-space: pre;\n      }\n\n    }\n  }\n} // #editor-container","$maxWidth: 800px;\n\n#creator-main {\n  max-width: $maxWidth;\n  margin: 0px auto;\n\n  padding: 10px;\n\n  font-family: $codeFontFamily;\n  font-size: 14px;\n\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n\n  overflow: auto;\n\n  .creator-meta {\n\n    border: 1px solid lightgrey;\n    border-radius: 4px;\n\n    margin-bottom: 10px;\n\n    .creator-input {\n      padding: 5px 10px;\n\n      .input-form {\n        &::before {\n          content: \"Input: {\";\n        }\n        &::after {\n          content: \"}\";\n        }\n        table { \n          margin-left: 20px;\n\n          border-collapse: separate;\n          border-spacing: 5px; \n          \n          .input-field {\n            .input-field-name {\n              color: $argNameColor;\n              &::after {\n                content: \": \"\n              }\n            }\n\n            input {              \n              width: 100%;\n              border: 1px solid $queryNameColor;\n              border-radius: 4px;\n              box-sizing: border-box;\n            }\n\n            .input-field-type {\n              &::before {\n                content: \" (\";\n              }\n              &::after {\n                content: \")\"\n              }\n              .type-name {\n                color: $typeNameColor;\n              }\n            }\n          }\n        }\n      }\n    }\n\n    .creator-controls {\n      text-align: right;\n      button {\n        padding: 8px 16px;\n        border: 1px solid grey;\n        background-color: whitesmoke;\n        border-radius: 4px;\n\n        &:disabled {\n          background-color: whitesmoke;\n          border-color: gainsboro;\n          color: gainsboro;\n        }\n      }\n    }\n\n  } //creator-meta\n\n  .creator-body {\n    \n    padding: 10px;\n    border: 1px solid lightgrey;\n    border-radius: 4px;\n\n    * {\n      box-sizing: border-box;\n    }\n\n    .creator-fieldset {\n\n      .creator-field-container {\n\n        .creator-field {\n          float: none;\n          clear: both;\n\n          height: 1.5em;\n          line-height: 1.5em;\n          margin: 2px 0px;\n          border-radius: 2px;\n\n          overflow: hidden;\n\n          cursor: pointer;\n\n          &:hover {\n            background-color: gainsboro;\n          }\n\n          .field-checkbox {\n            width: 1.5em;\n            height: 1.5em;\n            padding: 7px;\n            \n            .field-checkmark {\n              width: 100%;\n              height: 100%;\n\n              border: 1px solid grey;\n              border-radius: 50%;\n\n              &.checked {\n                background-color: grey;\n              }\n            }\n          }\n\n          .field-name {\n            color: $queryNameColor;\n            &.needed {\n              border-bottom: 1px dashed red;\n            }\n          }\n          .field-type {\n            margin-left: 5px;\n            .type-name {\n              color: $typeNameColor;\n            }\n          }\n\n          .field-description {\n            margin-left: 5px;\n            font-size: 12px;\n            color: grey;\n\n            overflow: hidden;\n          }\n\n          > div { \n            display: block;\n            margin: 0px;\n            padding: 0px;\n            height: 20px;\n            float: left;\n          }\n        }\n\n        .sub-fields {\n          margin-left: 30px;\n          display: block;\n          float: none;\n          clear: both;\n        }\n        \n      }\n\n    }\n  } //creator-body\n\n\n} //creator-main"],"sourceRoot":""}]);
